import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Modal } from 'antd';
import { actions } from '../../redux/actions';
import Login from '../login/Login';
import Donate from './Donate';

export default function Card(props) {

    const { card } = props;
    const dispatch = useDispatch();

    const [openModal, setOpenModal] = useState(false);

    const closeModal=()=>{
        setOpenModal(false);
    }
    return (
        <div className='Card row'>
            <div className="card col-12 mb-2" style={{ width: "18rem" }} onClick={() => {
                if (card.gift.numOfUsed < card.gift.amount)
                    setOpenModal(true)
            }}>
                {card && <div className={`card-body ${card.gift.numOfUsed >= card.gift.amount ? 'watermark notSelected' : ''}`}>
                    {/* {card && <div className={`card-body watermark notSelected`}> */}
                    <div className='d-flex align-items-center justify-content-center '>
                        {/* <GiftOutlined /> */}
                        <h3>{`${card.sum} `}</h3>
                    </div>
                    <b className="card-text">{card.text}</b>
                    {card.gift && <>
                        <p>  {card.gift.name}</p>
                        <Link to={`/gift-details/${card.gift._id}`} onClick={() => {
                            if (card.gift.numOfUsed < card.gift.amount)
                                dispatch(actions.getGiftById(card.gift))
                        }}>  </Link></>}
                    <p>{`  ${card.gift.amount - card.gift.numOfUsed}`}</p>
                </div>}
            </div>
            <Modal footer={false}
                title={` ${card.sum}   ${card.gift ? card.gift.name : '  '}`}
                width={800}
                bodyStyle={{ height: '85vh' }}
                centered={true}
                style={{ textAlign: 'center' }}
                visible={openModal}
                onCancel={closeModal}>
                <Donate card={card} close={closeModal} />
            </Modal>
        </div >
    )
}